import OptionCookie from "../../util/OptionCookie";
/**
 * Created by sam on 2017/5/25.
 * login 界面管理
 */
export default class LoginView
{
    public static init():void
    {
        $("#input_Phone_Email").focusin(function () {
            $(".error_info").hide()
        });
        $(".eye1-student").on("click",function () { $(this).hide(); $(".eye2-student").show(); $("#password").attr("type","text"); })
        $(".eye2-student").on("click",function () { $(this).hide(); $(".eye1-student").show(); $("#password").attr("type","password"); })
        $('#dropdownMenu1').on('click',function(){
            $('#dropdown-menu').toggle();
        });
        $(document).on('click',function(e){
            var targetDiv = $('#dropdownMenu1');
            if(!targetDiv.is(e.target) && targetDiv.has(e.target).length === 0){
                   $('#dropdown-menu').hide();
            }
        });

        document.getElementById("login-link").onclick = function(e) {
            document.getElementById('loginBox').style.display = "block";
        };
        document.getElementById("loginBoxCloseImg").onclick = function(e) {
            document.getElementById('loginBox').style.display = "none";
        };
        document.getElementById("loginButton").onclick = function(e) {
            LoginView.login();
        };
        document.getElementById("loginBox").addEventListener("keydown", function (event) {
            if (event.keyCode==13) LoginView.login();
        }, true);
    }

    public static login():void
    {
        function isEmail(str) {
            var reg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/
            return reg.test(str)
        }

        function isMobilePhone(str) {
            var myreg = /^(((13[0-9]{1})|(15[0-9]{1})|(17[0-9]{1})|(18[0-9]{1}))+\d{8})$/
            return myreg.test(str)
        }

        let user:any = {password: $("#password").val()};
        var userName = $("#input_Phone_Email").val();
        if (isEmail(userName)) {
            user.email = userName
        } else if (isMobilePhone(userName)) {
            user.email = userName
        } else {
            user.username = userName
        }
        var URL_ = "http://sso.youyoucode.cn/api/users/login";                     //http://sso.youyoucode.cn/api/users/login
        if(userName.length<1||user.password<1)$(".error_info").show();
        else $.ajax({
            url: URL_,
            type: "post",
            data: user,
            dataType:"json",
            success: function (data) {
                if (data.userId) {
                    localStorage.setItem('nick_name',userName);
                    localStorage.setItem('user_id',userName);
                    $('#studentLogin').hide();
                    location.reload();
                } else {
                    $(".error_info").show();
                }
            },
            error:function(err){
                $(".error_info").show();
            }
        });
    }

    public static checkLogin():void
    {
        if (localStorage.getItem("nick_name")){
            $('#loginBox').hide();
            document.getElementById("hackathonAccount").innerHTML = localStorage.getItem("nick_name");
            $('#dropdownMenu1').show();
            $('#toolbar-item-signup').hide();
            $('#divider').hide();
            $('#studentLogin').hide();
        }else{
            $('#toolbar-item-signup').show();
            $('#loginMenu1').show();
            $('#dropdownMenu1').hide();
        }
    }
}
