import CanvasView from "../view/sprite/CanvasView";
import DataManager from "../manager/DataManager";
import WorkerManager from "../manager/WorkerManager";
/**
 * Created by ken on 2017/08/03.
 * thumbnail
 * */

export default class ThumbnailUtil {
    public static worksPath():any
    {
        var width = Math.floor((document.body.clientWidth-73)/2);
        var height = document.body.clientHeight - 86;
        var size = (width>height)?height:width;
        if(WorkerManager.getInstance().getEditor().editor.getValue().indexOf("getElementById")>=0){
            var myiframe:any =document.getElementById("drawBoard");
            var myCanvas:any = myiframe.contentWindow.document.getElementById("canvas");
            var tmp: HTMLCanvasElement = document.createElement("canvas");
            tmp.width = 300;
            tmp.height = 300;
            tmp.getContext("2d").drawImage(myCanvas, 0, 0, size, size,0,0,300,300);
            var request = new egret.HttpRequest();
            request.responseType = egret.HttpResponseType.TEXT;
            request.open("http://"+location.hostname+"/python/resource/works/upload.php",egret.HttpMethod.POST);
            request.setRequestHeader("Content-Type","application/x-www-form-urlencoded");
            //设置图片跨域访问
            request.send("key=" + DataManager.getInstance().curWork.id+"&data="+tmp.toDataURL("image/png"));
        }else{
            var renderTexture:egret.RenderTexture = new egret.RenderTexture();
            renderTexture.drawToTexture(CanvasView.getInstance(),new egret.Rectangle(0, 0, size, size),300.0/size);
            var request = new egret.HttpRequest();
            request.responseType = egret.HttpResponseType.TEXT;
            request.open("http://"+location.hostname+"/python/resource/works/upload.php",egret.HttpMethod.POST);
            request.setRequestHeader("Content-Type","application/x-www-form-urlencoded");
            request.send("key=" + DataManager.getInstance().curWork.id+"&data="+renderTexture.toDataURL("image/png"));
        }
    }
    public static pagesPath(id:any):any
    {
        var width = Math.floor((document.body.clientWidth-73)/2);
        var height = document.body.clientHeight - 86;
        var size = (width>height)?height:width;
        if(WorkerManager.getInstance().getEditor().editor.getValue().indexOf("getElementById")>=0){
            var myCanvas:any = document.getElementById("canvas");
            var tmp: HTMLCanvasElement = document.createElement("canvas");
            tmp.width = 300;
            tmp.height = 300;
            tmp.getContext("2d").drawImage(myCanvas, 0, 0, size, size,0,0,300,300);
            var request = new egret.HttpRequest();
            request.responseType = egret.HttpResponseType.TEXT;
            console.log(location.hostname);
            request.open("http://"+location.hostname+"/python/resource/pages/upload.php",egret.HttpMethod.POST);
            request.setRequestHeader("Content-Type","application/x-www-form-urlencoded");
            //设置图片跨域访问
            //tmp.setAttribute('crossOrigin', 'anonymous');
            //tmp.onload = function () {
                request.send("key=" + id +"&data="+tmp.toDataURL("image/png"));
            //}
        }else{
            var renderTexture:egret.RenderTexture = new egret.RenderTexture();
            renderTexture.drawToTexture(CanvasView.getInstance(),new egret.Rectangle(0, 0, size, size),300.0/size);
            var request = new egret.HttpRequest();
            request.responseType = egret.HttpResponseType.TEXT;
            request.open("http://"+location.hostname+"/python/resource/pages/upload.php",egret.HttpMethod.POST);
            request.setRequestHeader("Content-Type","application/x-www-form-urlencoded");
            request.send("key=" + id +"&data="+renderTexture.toDataURL("image/png"));
        }
    }
}
