
import DataManager from "../../manager/DataManager";
/**
 * Created by sam on 2017/5/25.
 * library 界面管理
 */
export default class ImportProjectView{

    public static init():void
    {
        document.getElementById('import-button-confirm-id').onclick = function(e) {
            var txt = (<HTMLInputElement>document.getElementById("import-project-text")).value;
            DataManager.getInstance().callServerSocket(DataManager.GAME_IMPORT_WORK,{id:txt});
        };

        document.getElementById("file-7").onclick = function(e) {
            document.getElementById('backgroundBoardId').style.display="block";
            document.getElementById('import-project').style.display="block";
            (<HTMLInputElement>document.getElementById("import-project-text")).value="";
        };
    }
}
