import Scene from "../base/view/Scene";
import ConfigManager from "./ConfigManager";
/**
 * Created by sam on 2017/5/31.
 * 界面管理。
 */
export default class UIManager
{
    private _scenes:Scene[];
    private _stage:egret.Stage;
    public constructor()
    {

    }

    private static _instance: UIManager;
    static getInstance(): UIManager
    {
        if(this._instance == null)
        {
            this._instance = new UIManager();
        }
        return this._instance;
    }

    //初始化界面管理器
    public init(stage:egret.Stage): void
    {
        this._scenes = [];
        this._stage = stage;
    }

    //服务器返回的数据分派到各界面
    public serverBack(data:string):void
    {
        this._scenes[this._scenes.length-1].callback(data);
    }

    //层级scen层级管理
    public getRunningScene():Scene
    {
        return this._scenes[this._scenes.length-1];
    }

    public popScene():void
    {
        this._stage.removeChild(this._scenes[this._scenes.length-1]);
        this._scenes.pop();
        this._scenes[this._scenes.length-1].onEnter();
    }

    public pushScene(scene:Scene,data?:any):void
    {
        this._scenes.push(scene);
        this._stage.addChild(this._scenes[this._scenes.length-1]);
        this._scenes[this._scenes.length-1].onEnter();
        if (data) this._scenes[this._scenes.length-1].setData(data);
    }

    public getStage():egret.Stage
    {
        return this._stage;
    }

    public replaceScene(scene:Scene,data?:any):void
    {
        this.popScene();
        this._scenes.push(scene);
        this._stage.addChild(this._scenes[this._scenes.length-1]);
        this._scenes[this._scenes.length-1].onEnter();
        if (data) this._scenes[this._scenes.length-1].setData(data);
    }

    public showMessage(test:string):void
    {
        $("#system-notice-text").text(ConfigManager.getInstance().getLanguageByKey(test));
        document.getElementById('system-notice-text').style.display = "block";
        TweenLite.killTweensOf(this._stage);
        TweenLite.to(this._stage,1.5,{onComplete:function (){
            document.getElementById('system-notice-text').style.display = "none";
        }});
    }

    public showText(txt:string):void
    {
        $("#system-notice-text").text(txt);
        document.getElementById('system-notice-text').style.display = "block";
        TweenLite.killTweensOf(this._stage);
        TweenLite.to(this._stage,1.5,{onComplete:function (){
            document.getElementById('system-notice-text').style.display = "none";
        }});
    }

}
