
import Scene from "../../base/view/Scene";
import WorkerManager from "../../manager/WorkerManager";
import CanvasView from "../sprite/CanvasView";
import DataManager from "../../manager/DataManager";
import DocumentManager from "../../manager/DocumentManager";
import ConfigManager from "../../manager/ConfigManager";
import OpenProjectView from "../document/OpenProjectView";
import ThumbnailUtil from "../../util/ThumbnailUtil"  ;
import UIManager from "../../manager/UIManager";
import MouseUtil from "../engine/MouseUtil";

export default class MainScene extends Scene {
    private newOpened = true;
    private pages:any[];
    private readIndex:number;
    private isSended:boolean = false;
    private oldLines = 0;
    public constructor() {
        super();
        this.pages = [];
        this.readIndex = 0;
    }

    public setData(data:any):void {
        super.setData(data);
        ConfigManager.getInstance().init();
        this.addChild(CanvasView.getInstance());

        var spr1:egret.Sprite = new egret.Sprite();
        spr1.graphics.beginFill(0x00ff00, 0);
        spr1.graphics.drawRect(0, 0, 1000, 1000);
        spr1.graphics.endFill();
        this.addChild( spr1 );
        spr1.touchEnabled = true;
        spr1.addEventListener( egret.TouchEvent.TOUCH_BEGIN, this.onTouchBegin, this );
        spr1.addEventListener( egret.TouchEvent.TOUCH_MOVE, this.onTouchMove, this );
        spr1.addEventListener( egret.TouchEvent.TOUCH_END, this.onTouchEnd, this );

        WorkerManager.getInstance().init(this);

        if(window.location.href.indexOf("runner.html")>=0){
            WorkerManager.getInstance().getMainWork().runCode(localStorage.getItem("code"));
            return;
        }
        DataManager.getInstance().addEventListener(DataManager.SERVERCALLBACK,this.onServerDataReceived,this);
        DataManager.getInstance().callServerSocket(DataManager.ROUTE_LOGIN,{uid:DataManager.getInstance().uid,cell:localStorage.getItem('user_id')});
        if(window.location.href.indexOf("show.html")<0){
            DocumentManager.getInstance().init();
            WorkerManager.getInstance().getEditor().addEventListener("user_code_change", this.onGetUserCode, this);
        }else return;

        if (localStorage.getItem("nick_name")){
            var nick_name = localStorage.getItem("nick_name");
            $('#dropdown-login .dropdown-toggle').html(nick_name+"<img class='icon-arrow-down' src='resource/assets/dropdown-arrow.svg'>");//
            $('#finish-login').show();
            $('#navbar-login').hide();
        }
    }

    private onTouchBegin(evt:egret.TouchEvent):void{
        MouseUtil.getInstance().mouseX = evt.stageX;
        MouseUtil.getInstance().mouseY = evt.stageY;
        MouseUtil.getInstance().isClicked = true;
    }

    private onTouchMove(evt:egret.TouchEvent):void{
        MouseUtil.getInstance().mouseX = evt.stageX;
        MouseUtil.getInstance().mouseY = evt.stageY;
    }

    private onTouchEnd(evt:egret.TouchEvent):void{
        MouseUtil.getInstance().mouseX = evt.stageX;
        MouseUtil.getInstance().mouseY = evt.stageY;
        MouseUtil.getInstance().isClicked = false;
    }

    private onGetUserCode(e:egret.Event):void {
        var that = this;
        var text = WorkerManager.getInstance().getEditorText();
        if(DataManager.getInstance().studentWork){
            if(this.isSended==false){
                this.isSended = true;
                DataManager.getInstance().callServerSocket(DataManager.GAME_PASS_CODE,{room:DataManager.getInstance().inRoomName,id:DataManager.getInstance().studentWork.uid,code:text});
            }else{
                TweenLite.killTweensOf(this);
                TweenLite.to(CanvasView.getInstance(),2,{onComplete:function (){
                    that.isSended = false;
                    DataManager.getInstance().callServerSocket(DataManager.GAME_PASS_CODE,{room:DataManager.getInstance().inRoomName,id:DataManager.getInstance().studentWork.uid,code:text});
                }});
            }
        }else if(DataManager.getInstance().teacherID>0){
            UIManager.getInstance().showMessage("text_classroom_teacher_onshow");
        }else if( DataManager.getInstance().userinfo.vip-10>=0){
            UIManager.getInstance().showMessage("text_classroom_teacher_onlook");
        }else if(DataManager.getInstance().teacherCtrl==false || DataManager.getInstance().userinfo.vip-10>=0){                             // 初始老师进入状态
            if (undefined != DataManager.getInstance().curWork)
                DataManager.getInstance().curWork.codes = text == undefined ? "" : text;
            if (undefined != DataManager.getInstance().curWork && DataManager.getInstance().curWork.codes.length > 0 && this.newOpened==false){
                DataManager.getInstance().callServerSocket(DataManager.GAME_AUTOSAVE_WORK, {id: DataManager.getInstance().curWork.id, name: DataManager.getInstance().curWork.wname, code: DataManager.getInstance().curWork.codes});
            }
        }
        if(DataManager.getInstance().teacherCtrl && DataManager.getInstance().userinfo.vip-10>=0){
            DataManager.getInstance().callServerSocket(DataManager.GAME_PUBLISH_CODE,{uid:DataManager.getInstance().uid,room:DataManager.getInstance().inRoomName,code:DataManager.getInstance().curWork.codes});
        }else if(DataManager.getInstance().teacherLook>0){
            if(this.isSended==false){
                this.isSended = true;
                DataManager.getInstance().callServerSocket(DataManager.GAME_PASS_CODE,{room:DataManager.getInstance().inRoomName,id:DataManager.getInstance().teacherLook,code:text});
            }else{
                TweenLite.killTweensOf(this);
                TweenLite.to(CanvasView.getInstance(),2,{onComplete:function (){
                    that.isSended = false;
                    DataManager.getInstance().callServerSocket(DataManager.GAME_PASS_CODE,{room:DataManager.getInstance().inRoomName,id:DataManager.getInstance().teacherLook,code:text});
                }});
            }
        }
        if(DataManager.getInstance().autoRunMode==false) return;
        if(this.newOpened){
            this.newOpened = false;
            return;
        }
        WorkerManager.getInstance().getMainWork().refreshFrame();
        this.oldLines = WorkerManager.getInstance().getEditor().getValidCodeLines();
    }

    private updateWork():void {
        if (DataManager.getInstance().curWork){
            WorkerManager.getInstance().setEditorText(DataManager.getInstance().curWork.codes);
            document.getElementById('project-title-in').innerHTML = DataManager.getInstance().curWork.wname;
            //$("#editor-pane-nav-snippets-menu-input").text(DataManager.getInstance().curWork.jsplus);
            WorkerManager.getInstance().getMainWork().refreshFrame();
        }
    }

    public openNewProject():void {
        this.newOpened = true;
        this.updateWork();
        WorkerManager.getInstance().getMainWork().refreshFrame();
    }

    protected onServerDataReceived(event: egret.Event):void {
        let rawData: any = event.data;
        let route: string = rawData.route;
        let dat: any = rawData.data.data;
        if (route == DataManager.ROUTE_LOGIN){
            if(localStorage.getItem('nick_name') && dat.userinfo.nickName !== localStorage.getItem('nick_name')){
                dat.userinfo.nickName = localStorage.getItem('nick_name');
                DataManager.getInstance().callServerSocket(DataManager.GAME_SET_NICKNAME,{uid:DataManager.getInstance().uid,name:localStorage.getItem('nick_name')});
            }
            DataManager.getInstance().userinfo = dat.userinfo;
            DataManager.getInstance().works = [];
            for(var j=0;j<dat.works.length;j++){
                if(dat.works[j].mode+0>0) DataManager.getInstance().works.push(dat.works[j]);
            }
            if (window.location.href.indexOf("show.html") < 0){
                DataManager.getInstance().works.sort(function (a, b) {
                    return b.updateTime - a.updateTime;
                });
                if(DataManager.getInstance().works.length>0) DataManager.getInstance().curWork = DataManager.getInstance().works[0];
                document.getElementById("splash-cover").style.display="none";
                this.newOpened = true;
                if(window.location.href.indexOf("v=") >= 0){
                    var vals = window.location.href.split("v=");
                    if (vals.length > 1) DataManager.getInstance().callServerSocket(DataManager.GAME_GET_WORK,{id:vals[1]});
                }else this.updateWork();
            }else{
                var vals = window.location.href.split("v=");
                if (vals.length > 1) DataManager.getInstance().callServerSocket(DataManager.GAME_GET_WORK,{id:vals[1]});
            }
        }else if(route == DataManager.GAME_NEW_WORK){
            if(dat.work){
                dat.work.mode = 0;
                DataManager.getInstance().curWork = dat.work;
                DataManager.getInstance().works.push(dat.work);
                DataManager.getInstance().works.sort(function (a, b) {
                    return b.updateTime - a.updateTime;
                });
                this.updateWork();
                document.getElementById('new-project').style.display = "none";
                document.getElementById('backgroundBoardId').style.display="none";
            }else{
                document.getElementById('new-project-text-notice1').style.display = "block";
                TweenLite.to(this,1.5,{onComplete:function (){
                    document.getElementById('new-project-text-notice1').style.display = "none";
                }});
                document.getElementById('new-project').style.display = "block";
                document.getElementById('backgroundBoardId').style.display = "block";
                return;
            }
            // 打开本地作品之后点赞功能消失
            DataManager.getInstance().isCommunitWork = false;
        }else if(route == DataManager.GAME_IMPORT_WORK){
            document.getElementById('backgroundBoardId').style.display="none";
            document.getElementById('import-project').style.display="none";
            if(dat.work){
                DataManager.getInstance().curWork = dat.work;
                DataManager.getInstance().works.push(dat.work);
                DataManager.getInstance().works.sort(function (a, b) {
                    return b.updateTime - a.updateTime;
                });
                this.updateWork();
                UIManager.getInstance().showMessage("text_prompt_clone");
            }else{
                UIManager.getInstance().showMessage("text_failed_clone");
            }
        }else if(route == DataManager.GAME_CLONE_WORK){
            if(dat.work){
                DataManager.getInstance().curWork = dat.work;
                DataManager.getInstance().works.push(dat.work);
                DataManager.getInstance().works.sort(function (a, b) {
                    return b.updateTime - a.updateTime;
                });
                document.getElementById('clone-project').style.display = "none";
                document.getElementById('backgroundBoardId').style.display="none";
                this.updateWork();
                UIManager.getInstance().showMessage("text_prompt_clone");
                //ThumbnailUtil.worksPath();
                window.location.href = 'http://www.youyoucode.cn/python/';
            }else{
                UIManager.getInstance().showMessage("text_nameWrong_project");
                return;
            }
        }else if(route == DataManager.GAME_RENAME_WORK){
            if(dat.work){
                DataManager.getInstance().curWork.wname = dat.work.wname;
                DataManager.getInstance().works.sort(function (a, b) {
                    return b.updateTime - a.updateTime;
                });
                this.updateWork();// 保存缩略图
                UIManager.getInstance().showMessage("text_prompt_rename");
                ThumbnailUtil.worksPath();
            }else{
                UIManager.getInstance().showMessage("text_nameWrong_project");
            }
        }else if(route == DataManager.GAME_SAVE_WORK){
            UIManager.getInstance().showMessage("text_prompt_save");
            var finded = false;
            for(var j=0;j<DataManager.getInstance().works.length;j++){
                if (DataManager.getInstance().works[j].id - dat.work.id == 0) finded = true;
            }
            if(finded==false){
                DataManager.getInstance().works.push(dat.work);
            }
            for(var j=0;j<DataManager.getInstance().works.length;j++){
                if (DataManager.getInstance().works[j].id - dat.work.id == 0){
                    DataManager.getInstance().works[j].wname = dat.work.wname;
                    DataManager.getInstance().works[j].codes = dat.work.codes;
                    DataManager.getInstance().works[j].updateTime = dat.work.updateTime;
                }
            }
            DataManager.getInstance().works.sort(function (a, b) {
                return b.updateTime - a.updateTime;
            });
            DataManager.getInstance().curWork = dat.work;
            this.updateWork();
            ThumbnailUtil.worksPath();                                                                                  // 保存之后点赞功能消失
            DataManager.getInstance().isCommunitWork = false;
            if(finded==false) window.location.href = 'http://www.youyoucode.cn/python/';
        }else if(route == DataManager.GAME_AUTOSAVE_WORK){
            var finded = false;
            for(var j=0;j<DataManager.getInstance().works.length;j++){
                if (DataManager.getInstance().works[j].id - dat.work.id == 0) finded = true;
            }
            if(finded==false){
                DataManager.getInstance().curWork = dat.work;
                DataManager.getInstance().works.push(dat.work);
                DataManager.getInstance().works.sort(function (a, b) {
                    return b.updateTime - a.updateTime;
                });
                this.updateWork();
                window.location.href = 'http://www.youyoucode.cn/python/';
            }
        }else if(route == DataManager.GAME_GET_WORK){
            if(dat.work) DataManager.getInstance().curWork = dat.work;
            if(window.location.href.indexOf("show.html")>=0){
                if(dat.work) {
                    $("#game-area").hide();
                    WorkerManager.getInstance().setEditorText(dat.work.codes);
                    //$("#editor-pane-nav-snippets-menu-input").text(dat.work.jsplus);
                    WorkerManager.getInstance().getMainWork().refreshFrame();
                }
            }else this.updateWork();
            document.getElementById("splash-cover").style.display="none";
        }else if(route == DataManager.GAME_DELETE_WORK){
            var works = [];
            for(var j=0;j<DataManager.getInstance().works.length;j++){
                if(dat.id-DataManager.getInstance().works[j].id!=0){
                    works.push(DataManager.getInstance().works[j]);
                }
            }
            DataManager.getInstance().works = works;
            if(dat.id-DataManager.getInstance().curWork.id==0){
                if(DataManager.getInstance().works.length>0){
                    DataManager.getInstance().curWork = DataManager.getInstance().works[0];
                    this.updateWork();
                }
            }
            OpenProjectView.updateOpenProject();
        }
    }

    public callback(dat):void {

    }
}