
import EngineImage from "./EngineImage";
import CanvasView from "../sprite/CanvasView";
import EngineSprite from "./EngineSprite";
import WorkerManager from "../../manager/WorkerManager";
import ColorUtil from "../../util/ColorUtil";
import WorldView from "../sprite/WorldView";

export default class GameUtil
{
    public constructor() {
        egret.ImageLoader.crossOrigin = "anonymous";
    }

    private static _instance: GameUtil;
    static getInstance(): GameUtil {
        if(this._instance == null){
            this._instance = new GameUtil();
        }
        return this._instance;
    }

    public line(x:number,y:number,w:number,h:number,r:number,c:string):egret.Sprite {
        var sp = new EngineSprite();
        sp.drawLine(x, y, w, h, r, c);
        this.addChild(sp);
        return WorkerManager.getInstance().saveObj(sp);
    }

    public circle(x:number,y:number,r:number,c:string):egret.Sprite {
        var sp = new EngineSprite();
        sp.drawCircle(x, y, r, c);
        this.addChild(sp);
        return WorkerManager.getInstance().saveObj(sp);
    }

    public triangle(x1:number,y1:number,x2:number,y2:number,x3:number,y3:number,c:string):egret.Sprite {
        var sp = new EngineSprite();
        sp.drawTriangle(x1, y1, x2, y2, x3, y3, c);
        this.addChild(sp);
        return WorkerManager.getInstance().saveObj(sp);
    }

    public rectangle(x:number,y:number,w:number,h:number,c:string):egret.Sprite {
        var sp = new EngineSprite();
        sp.drawRect(x, y, w, h, c);
        this.addChild(sp);
        return WorkerManager.getInstance().saveObj(sp);
    }

    public ellipse(x:number,y:number,w:number,h:number,c:string):egret.Sprite {
        var sp = new EngineSprite();
        sp.drawEllipse(x, y, w, h, c);
        this.addChild(sp);
        return WorkerManager.getInstance().saveObj(sp);
    }

    public sprite():egret.Sprite {
        var sp = new EngineSprite();
        this.addChild(sp);
        return WorkerManager.getInstance().saveObj(sp);
    }

    public image(url:string,x:number,y:number,w:number,h:number):EngineImage {
        var sp = new EngineImage();
        sp.setPosition(x, y);
        sp.setSize(w, h);
        sp.loadpic(url);
        this.addChild(sp);
        return WorkerManager.getInstance().saveObj(sp);
    }

    public playAudio(url:string):void {
        var explode=new Audio(url);
        explode.play();
    }

    public sleep(t:number):void {
        WorkerManager.getInstance().isPaused = true;
        TweenLite.delayedCall(t/1000,function(){
            WorkerManager.getInstance().isPaused = false;
        });
    }

    public text(x:number,y:number,s:number,v:string,c:string):egret.Sprite {
        var label:egret.TextField = new egret.TextField();
        label.x = x;
        label.y = y;
        label.size = s;
        label.text = v;
        label.textColor = ColorUtil.getColor(c);
        this.addChild(label);
        return WorkerManager.getInstance().saveObj(label);
    }

    public sphere():egret3d.Mesh{
        var geom:egret3d.SphereGeometry = new egret3d.SphereGeometry();
        var mater:egret3d.TextureMaterial = new egret3d.TextureMaterial();
        var sphere:egret3d.Mesh = new egret3d.Mesh(geom, mater);
        WorldView.getInstance().view3D.addChild3D(sphere);
        return sphere;
    }

    public rand(from:number,to:number):number {
        return Math.floor((to-from)*Math.random())+from;
    }

    public addChild(obj:any):void {
        CanvasView.getInstance().addChild(obj);
    }

    public removeChild(obj:egret.Sprite):void {
        CanvasView.getInstance().removeChild(obj);
    }

    public onLoop(fn,millisec:number = 0):void {
        window.setInterval(function(){
            WorkerManager.getInstance().getMainWork().runFuction(fn);
        },millisec);
    }
}