
import PlistParser from "../../util/PlistParser";
import CanvasView from "../sprite/CanvasView";
export default class EngineImage extends egret.Sprite
{
    private _bitmap:egret.Bitmap;
    public _oldW = 0;
    public _oldH = 0;
    public constructor()
    {
        super();
    }

    public loadpic(url:string):void
    {
        if(url.indexOf("ttp:")>0){
            RES.getResByUrl(url,this.onComplete,this,RES.ResourceItem.TYPE_IMAGE);
        }else{
            this._bitmap = new egret.Bitmap(new PlistParser("res").getTexture(url + ".png"));
            this.addChild(this._bitmap);
        }
    }

    private onComplete(event:any):void
    {
        this._bitmap = new egret.Bitmap(<egret.Texture>event);
        this.addChild(this._bitmap);
        if(this._oldW>0) {
            this.scaleX = this._oldW / this._bitmap.width;
            this.scaleY = this._oldH / this._bitmap.height;
        }else{
            this._oldW = this._bitmap.width;
            this._oldH = this._bitmap.height;
        }
    }

    public hit(bit:EngineImage):Boolean
    {
        if(this.x+this._oldW<bit.x || bit.x+bit._oldW<this.x || this.y+this._oldH<bit.y || bit.y+this._oldH<this.y)
            return false;
        else return true;
    }

    public setPosition(x:number,y:number)
    {
        this.x = x;
        this.y = y;
    }

    public moveTo(x:number,y:number,t?:number)
    {
        if(t==null) t = 1;
        TweenLite.to(this,t,{x:x, y:y});
    }

    public scaleTo(s:number,t?:number)
    {
        if(t==null) t = 1;
        TweenLite.to(this,t,{scaleX:s, scaleY:s});
    }

    public setSize(w:number,h:number)
    {
        if(w>0) {
            this._oldW = w;
            this._oldH = h;
            this.width = w;
            this.height = h;
        }
    }

    public dispose()
    {
        this._bitmap.texture.dispose();
    }
}