import TimeUtil from "../../util/TimeUtil";
import DataManager from "../../manager/DataManager";
import UIManager from "../../manager/UIManager";
/**
 * Created by sam on 2017/5/25.
 * open project 界面管理
 */
export default class OpenProjectView{
    public static initOpenProject():void
    {
        document.getElementById("file-1").onclick = function(e) {
            OpenProjectView.updateOpenProject();
        };
    }

    public static updateOpenProject():void
    {
        var str = "";
        var jstr = "onerror=\"javascript:this.src ='resource/works/default.png'\"";
        for (var i = 0; i < DataManager.getInstance().works.length; i++) {
            str += "<div class = 'image-padding'>"
                +     "<div class = 'image-box' id = work_" + DataManager.getInstance().works[i].id + ">"
                +        "<img class='open-img' src='resource/works/work_" + DataManager.getInstance().works[i].id + ".png?v=" + Math.random() + "' " + jstr + "/>"
                +     "</div>"
                +     "<div class = 'image-time'>" + TimeUtil.getTime(DataManager.getInstance().works[i].updateTime)  + "</div>"
                +     "<div class = 'image-name'>" + DataManager.getInstance().works[i].wname + "</div>"
                +     "<div class = 'delete-img' id = icon_" + DataManager.getInstance().works[i].id + "><img src='resource/assets/delete.png'/></div>"
                +   "</div>";
            str += "<div class = 'image-divider'></div>";
        }
        document.getElementById("open-project-content").innerHTML = "" + str;
        var that = this;
        var works:any = DataManager.getInstance().works;
        for(var i=0;i<works.length;i++){
            if(works[i]) {
                var id = works[i].id;
                document.getElementById("work_" + works[i].id).onclick = function (e) {
                    var tt: any = e.currentTarget;
                    tt = tt.id.split("_");
                    var wid = parseInt(tt[1]);
                    for (var j = 0; j < works.length; j++) {
                        if (works[j].id - wid == 0) {
                            if (DataManager.getInstance().curWork.id - wid != 0) {
                                DataManager.getInstance().curWork = works[j];
                                UIManager.getInstance().getRunningScene().openNewProject();
                            }
                            document.getElementById('open-project').style.display = "none";
                            document.getElementById('backgroundBoardId').style.display = "none";
                            break;
                        } 
                    }
                    // 打开作品之后点赞功能消失
                    DataManager.getInstance().isCommunitWork = false;
                };
                document.getElementById("icon_" + DataManager.getInstance().works[i].id).onclick = function (e) {
                    var tt: any = e.currentTarget;
                    tt = tt.id.split("_");
                    var wid = parseInt(tt[1]);
                    var del =  document.getElementById('del-dialog');
                    var yesBtn =  document.getElementById('yes-btn');
                    var noBtn =  document.getElementById('no-btn');
                    var btnW =  document.getElementById('btn-w');
                    // 如果只剩一个项目，那么提示不能删除
                    if(works.length == 1) {
                        UIManager.getInstance().showMessage("text_prompt_deleate");
                    }else{
                        del.style.display = 'block';
                    }
                    btnW.onclick = function(e){
                        var dat:any = e.target;
                        var id = dat.id;
                        if(id === 'yes-btn'){
                            del.style.display = 'none';
                            DataManager.getInstance().callServerSocket(DataManager.GAME_DELETE_WORK,{id:wid});
                        }else{
                            del.style.display = 'none';
                            return;
                        }
                    };
                };
            }
        }
        document.getElementById('backgroundBoardId').style.display="block";
        document.getElementById('open-project').style.display="block";
    }
}
