export default class ColorUtil{
    public static getColor(n:string):any
    {
        if(n=="yellow") return 0xffff00;
        else if(n=="red") return 0xff0000;
        else if(n=="green") return  0x00ff00;
        else if(n=="blue") return 0x0000ff;
        else if(n=="black") return 0x000000;
        else if(n=="white") return 0xffffff;
        else if(n=="pink") return 0xffc0cb;
        else if(n=="purple") return 0x9400d3;
        else if(n=="brown") return 0xa52a2a;
        else if(n=="orange") return 0xffa500;
        return 0xffffff;
    }
}
