
import CodeWorker from "../worker/CodeWorker";
import EditorView from "../view/sprite/EditorView";
/**
 * Created by sam on 2017/5/31.
 * 工人管理。
 */
export default class WorkerManager
{
    private _mainWorker:CodeWorker;
    private _editorView:EditorView;
    private _markers:any[];
    private funcNames:Object = {};
    private _language:string = "python";

    public isPaused:boolean = false;

    public get language():string
    {
        return this._language;
    }

    public set language(l:string)
    {
        this._language = l;
    }

    public constructor()
    {
        this._markers = [];
    }

    private static _instance: WorkerManager;
    static getInstance(): WorkerManager
    {
        if(this._instance == null)
        {
            this._instance = new WorkerManager();
        }
        return this._instance;
    }

    public init(actor:any):void
    {
        this._mainWorker = new CodeWorker(actor);
        this._editorView = new EditorView();
        this._editorView.init();
    }

    public changeLangue(l:string):void
    {
        if(this.language!=l) {
            if (this._editorView) this._editorView.dispose();
            this._editorView = new EditorView();
            this._editorView.init(l);
            this.updateHighLight();
            this.language = l;
        }
    }

    public saveFunctionName(l:number,v:string):void
    {
        this.funcNames[l] = v;
    }

    public getFunctionName(l:number):string
    {
        return this.funcNames[l];
    }

    public getMainWork():CodeWorker
    {
        return this._mainWorker;
    }

    public getEditor():EditorView
    {
        return this._editorView;
    }

    public dispose():void
    {
        this._mainWorker.dispose();
    }

    public updateHighLight():void
    {
        this._markers = [];                               //清除编辑器标记
        if (this._mainWorker.getEngine().lastAST)                       //设置高亮部分
        {
            this.setAstCodeHighLight(this._mainWorker.getEngine().lastAST);
        }
    }

    public setEditorText(text:string):void
    {
        this._editorView.editor.setValue(text, 1);
    }

    public getEditorText():string
    {
        return this._editorView.editor.getSession().getValue();
    }

    public insertSnippet(snippet:any)
    {
        this._editorView.editor['completer'] && this._editorView.editor['completer'].insertMatch(snippet, this._editorView.editor);
    }

    public getEffectiveCodeLine():number
    {
        return this._editorView.getValidCodeLines();
    }

    /**
     * 不同语言的语法树结构不完全相同，单独处理高亮显示
     * @param ast
     */
    private setAstCodeHighLight(ast:any):void
    {
        switch (this._editorView.language)
        {
            case 'javascript':
            {
                this.setJSAstCodeHighLight(ast);
                break;
            }
            case 'python':
            {
                this.setPythonAstCodeHighLight(ast);
                break;
            }
        }
    }

    public saveObj(obj:any):any
    {
        var line = 0;
        if(this._mainWorker._codeEngine._aether.esperEngine.evaluator.topFrame.ast.originalRange) line = this._mainWorker._codeEngine._aether.esperEngine.evaluator.topFrame.ast.originalRange.start.row;
        var funcName = this.getFunctionName(line);
        if(funcName) this._mainWorker._codeEngine.addObject(funcName,obj);
        return obj;
    }

    /**
     * JavaScript 高亮设置
     */
    private setJSAstCodeHighLight(ast:any):void
    {
        let range = ast.loc;
        let Range = ace.require('./range').Range;
        let rr = new Range(range.start.line - 1, range.start.column, range.end.line - 1, range.end.column);
        this._markers.push(this._editorView.editor.getSession().addMarker(rr,'executing', 'text', true));
        this.highlightCurrGutterLine(range.start.line);
    }

    /**
     * Python 高亮设置
     */
    private setPythonAstCodeHighLight(ast:any):void
    {
        let range = ast.originalRange;
        if (!range) return;
        let startLine = range.start.row;
        let startColumn = range.start.col;
        let endLine = range.end.row;
        let endColumn = range.end.col;
        let initStartLine = startLine;
        let initEndColumn = endColumn;
        if (ast.srcName && ast.type === 'CallExpression') {
            let lineNum:number = this._editorView.editor.getSession().getLength();
            let srcPart:string = ast.srcName.split('(')[0];
            let line:string = this._editorView.editor.getSession().getLine(initStartLine);
            while (line.indexOf(srcPart) === -1)
            {
                line = this._editorView.editor.getSession().getLine(++initStartLine);
                if (line.indexOf(srcPart) != -1)
                {
                    initEndColumn = srcPart.length;
                }
                if (initStartLine >= lineNum)
                {
                    initStartLine = startLine;
                    initEndColumn = endColumn;
                    break;
                }
            }
            startLine = endLine = initStartLine;
            endColumn = initEndColumn;
        }
        let Range = ace.require('./range').Range;
        let rr = new Range(startLine, startColumn, endLine, endColumn);
        this._markers.push(this._editorView.editor.getSession().addMarker(rr,'executing', 'text', true));

        this.highlightCurrGutterLine(startLine);
        if (ast.srcName && ast.srcName.indexOf('.') != -1)
        {

        }
    }

    public highlightCurrGutterLine(row:number)
    {
        //运行时,箭头指向的变化
        let docLength = this._editorView.editor.getSession().getLength();
        for(let i = 0; i < docLength; ++ i)
        {
            this._editorView.editor.renderer.removeGutterDecoration(i, 'executing');
        }
        if(row>0){
            this._editorView.editor.renderer.addGutterDecoration(row, 'executing');
            this._editorView.editor.clearSelection();
            this._editorView.editor.moveCursorTo(row);
            this._editorView.editor.renderer.scrollToRow( row );
        }
    }

}
