
import ColorUtil from "../../util/ColorUtil";
export default class EngineSprite extends egret.Sprite
{
    public constructor()
    {
        super();
    }

    public drawRect(x:number,y:number,w:number,h:number,c:string) {
        this.graphics.beginFill(ColorUtil.getColor(c));
        this.graphics.drawRect(x,y,w,h);
        this.graphics.endFill();
    }

    public drawEllipse(x:number,y:number,w:number,h:number,c:string) {
        this.graphics.beginFill(ColorUtil.getColor(c));
        this.graphics.drawEllipse(x,y,w,h);
        this.graphics.endFill();
    }

    public drawCircle(x:number,y:number,r:number,c:string) {
        this.graphics.beginFill(ColorUtil.getColor(c));
        this.graphics.drawCircle(x,y,r);
        this.graphics.endFill();
    }

    public drawArc(x: number, y: number, radius: number, startAngle: number, endAngle: number,c:string) {
        this.graphics.beginFill(ColorUtil.getColor(c));
        this.graphics.drawArc(x,y,radius,startAngle,endAngle);
        this.graphics.endFill();
    }

    public drawTriangle(x1:number,y1:number,x2:number,y2:number,x3:number,y3:number,c:string) {
        this.graphics.beginFill(ColorUtil.getColor(c));
        this.graphics.moveTo(x1,y1);
        this.graphics.lineTo(x2,y2);
        this.graphics.lineTo(x3,y3);
        this.graphics.lineTo(x1,y1);
        this.graphics.endFill();
    }

    public drawLine(x:number,y:number,w:number,h:number,r:number,c:string) {
        this.graphics.beginFill(ColorUtil.getColor(c));
        this.graphics.lineStyle(r,ColorUtil.getColor(c));
        this.graphics.moveTo(x,y);
        this.graphics.lineTo(w,h);
        this.graphics.endFill();
    }

    public drawText(x:number,y:number,s:number,v:string,c:string) {
        var label:egret.TextField = new egret.TextField();
        label.x = x;
        label.y = y;
        label.size = s;
        label.text = v;
        label.textColor = ColorUtil.getColor(c);
        this.addChild(label);
    }

    public setPosition(x:number,y:number) {
        this.x = x - this.width/2;
        this.y = y - this.height/2;
    }

    public moveTo(x:number,y:number,t?:number) {
        if(t==null) t = 1;
        TweenLite.to(this,t,{x:x, y:y});
    }

    public rotateTo(r:number,t?:number) {
        if(t==null) t = 1;
        TweenLite.to(this,t,{rotate:r});
    }

    public scaleTo(t:number,s:number) {
        TweenLite.to(this,t,{scaleX:s, scaleY:s});
    }

    public setSize(w:number,h:number) {
        if(w>0){
            this.scaleX = w / this.width;
            this.scaleY = h / this.height;
        }
    }

    public clear(){
        this.graphics.clear();
    }

    public dispose()
    {
        this.graphics.clear();
        this.removeChildren();
    }
}