
export default class KeyboradUtil
{
    private static _instance: KeyboradUtil;
    static getInstance(): KeyboradUtil {
        if(this._instance == null){
            this._instance = new KeyboradUtil();
        }
        return this._instance;
    }

    public keyCode:number = 0;

    public constructor() {
        document.onkeydown = function(e){
            KeyboradUtil.getInstance().keyCode = window.event ? e.keyCode :e.which;
        }

        document.onkeyup = function () {
            KeyboradUtil.getInstance().keyCode = 0;
        }
    }

}