import CodeEngine from "../../worker/CodeEngine";

export default class CanvasView extends egret.Sprite
{
    private static _instance: CanvasView;
    private sceneObjects:any[];
    private updateIndex:any;
    static getInstance(): CanvasView
    {
        if(this._instance == null)
        {
            this._instance = new CanvasView();
        }
        return this._instance;
    }

    public constructor()
    {
        super();
        this.sceneObjects = [];
    }

    private stopFrame():void
    {
        if(this.updateIndex){
            clearInterval(this.updateIndex);
            this.updateIndex = null;
        }
    }

    public refresh():void
    {
        this.stopFrame();
        this.removeChildren();
        this.sceneObjects = [];
    }
}