
import IEventDispatcher = egret.IEventDispatcher;
import CodeEngine from "./CodeEngine";
import CodeActor from "./CodeActor";
import WorkerManager from "../manager/WorkerManager";
import GameUtil from "../view/engine/GameUtil";
import ProblemAlertView from "../view/sprite/ProblemAlertView";
import KeyboradUtil from "../view/engine/KeyboradUtil";
import MouseUtil from "../view/engine/MouseUtil";
import WorldView from "../view/sprite/WorldView";

/**
 * Created by sam on 2017/5/22.
 * 用户代码的处理类，包含编辑器和代码执行引擎两个部分。
 */

export default class CodeWorker extends egret.EventDispatcher
{
    public _codeEngine:CodeEngine;
    private _actor:CodeActor;
    private _isComplete:boolean = false;                            //是否彻底执行完所有动作，代码引擎执行

    public constructor(actor?:CodeActor, target?:IEventDispatcher)
    {
        super(target);
        this._actor = actor;
        this._codeEngine = new CodeEngine("python");
    }

    public getEngine():CodeEngine
    {
        return this._codeEngine;
    }

    public get actor():CodeActor
    {
        return this._actor;
    }

    public runFuction(fn:any, args?:any):void
    {
        this._codeEngine.runFuction(fn,args);
    }

    public stepCode():void
    {
        this._codeEngine.runCode();
    }

    public showErrorInfo(message, url, linenumber):void {
        var error = {};
        error['type'] = 'error';
        error['row'] = "Line" + (linenumber-10 || 1);
        error['text'] = message;
        ProblemAlertView.getInstance().setProblems([error]);
    }

    public refreshFrame():void {
        //var lleft = document.getElementById("drawBoard").offsetLeft;
        //document.getElementById("drawBoard").outerHTML = '<iframe id="drawBoard" frameborder="0"></iframe>';

        this._codeEngine.reset();
        this._codeEngine.setCode(WorkerManager.getInstance().getEditor().editor.getValue());
        this._codeEngine.compile();
        let compileInfo = this._codeEngine.getCompileInfo();
        var errors = [];
        if (compileInfo) {
            errors = compileInfo['problems']['errors'];
            errors.forEach(function (error) {
                error['type'] = 'error';
                error['row'] = (error['range'] && error['range'][0] && error['range'][0]['row']) || 1;
                error['text'] = error['message'];
            });
        }
        WorkerManager.getInstance().getEditor().editor.renderer.setAnnotations(errors);
        if(errors.length<1){
            localStorage.setItem('code',WorkerManager.getInstance().getEditor().editor.getValue());
            $("#drawBoard").attr("src","runner.html");
        }
    }

    public runCode(code:any):void
    {
        this._codeEngine.reset();
        this._codeEngine.setCode(code);
        this._codeEngine.compile();
        //this._codeEngine.initFunction();
        WorldView.getInstance().initWork(this._codeEngine);
        this._codeEngine.addObject("game",GameUtil.getInstance());
        this._codeEngine.addObject("mouse",MouseUtil.getInstance());
        this._codeEngine.addObject("keyboard",KeyboradUtil.getInstance());
        this._codeEngine.stepCode();
    }

    private reset():void
    {
        this._codeEngine.reset();
        this._isComplete = false;
    }

    public stop():void
    {
        this.reset();
    }

    public dispose():void
    {
        this.reset();
        this._codeEngine.dispose();
        this._actor.dispose();
        this._actor = null;
    }
}
