
import OpenProjectView from "../view/document/OpenProjectView";
import CloneView from "../view/document/CloneView";
import LeftBarView from "../view/document/LeftBarView";
import NewProjectView from "../view/document/NewProjectView";
import MenuContentView from "../view/document/MenuContentView";
import LoginView from "../view/document/LoginView";
import AppMain from "../view/document/AppMain";
import ImportProjectView from "../view/document/ImportProjectView";
/**
 * Created by sam on 2017/5/31.
 *   网页文件管理。
 */

export default class DocumentManager
{
    private static _instance: DocumentManager;
    static getInstance(): DocumentManager
    {
        if(this._instance == null)
        {
            this._instance = new DocumentManager();
        }
        return this._instance;
    }

    public init():void
    {
        OpenProjectView.initOpenProject();
        LeftBarView.init();
        CloneView.init();
        NewProjectView.init();
        ImportProjectView.init();
        MenuContentView.init();
        LoginView.init();
        AppMain.init();

    }
}
